[data-theme=business] {
  --blue: #36414c;
  --white: #F3F5F9;
  --bg_white: #EDEDED;
  --navbar-bg: var(--blue);
  --bg-color: var(--white);
  color-scheme: light;
}
[data-theme=business] .widget.links-widget-box {
  border: 1px solid var(--bg_white);
}
[data-theme=business] .widget.spacer {
  background: var(--bg_white);
  padding: 1px;
  max-height: 1px;
}